import axios from "axios";
import jwt_decode from "jwt-decode";


// const API_URL = "http://localhost:8081";

const API_URL = "https://9a1f-41-94-163-4.ngrok-free.app";



const signup = (login, password) => {
  return axios
    .post(API_URL + "/signup", {
      login,
      password,
    })
    .then((response) => {
      if (response.data.accessToken) {
        localStorage.setItem("user", JSON.stringify(response.data));
      }

      return response.data;
    });
};

const login = (login, password) => {
  return axios
    .post(API_URL + "/login", {
      login,
      password,
    })
    .then((response) => {
      if (response.data) {
         localStorage.setItem('user', response.data);
      }
     
      return response.data;
    });
};


const logout = () => {
  console.log('token no logout: ',localStorage.getItem("user"));
  localStorage.removeItem("user");
  localStorage.clear()
  console.log('token no logout depois de clean: ',localStorage.getItem("user"));

 
};

const getCurrentUser = () => {
  //window.alert(JSON.parse(localStorage.getItem("currentUser")));
  const decoded = jwt_decode(localStorage.getItem("user"));
  return decoded;
};

const authService = {
  signup,
  login,
  logout,
  getCurrentUser,
};

export default authService;