import React, { useEffect, useState } from 'react'
import authService from '../../../utils/services/auth';
import { MoonLoader } from 'react-spinners';

let usuario = null
export default function Perfil() {

    const [carregando, setCarregando] = useState(true);


    useEffect(() => {
        console.log('====Entrou aqui');
        usuario = authService.getCurrentUser();
        console.log(' usuario.authorities ', usuario.authorities.includes('ADMIN'));
        setTimeout(() => {
            setCarregando(false);
        }, 5000);
        //  ? init() : usuarioLagado();
        // init();
    }, [])

    return (
        <div>
            {carregando ? (
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
                    <MoonLoader color="#36d7b7" />
                </div>

            ) : (
                <div>
                    {
                        usuario.authorities.includes('ADMIN') && <div> Perfil de Amin</div>

                    }{
                        usuario.authorities[0] === 'ESTUDANTE' && <div> Perfil de Estudante</div>

                    }

                </div>
            )}
        </div>
    );

}